.upload-container {
    width: 55%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
}

.upload-card {
    width: 50%;
    height: 80%;
    display: flex;
    flex-direction: column;
    background-color: white;
    justify-content: center;
    align-items: center;
    gap: 10px;
    box-shadow: 0px 0px 5px 1px #d3caca;
}

.upload {
    font-size: 22px;
    text-transform: uppercase;
    letter-spacing: 1px;
    margin-bottom: 16px;
}

.content-input {
    width: 80%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 1px solid black;
    padding: 0 0 0 4px;
    gap: 12px;
}

.progress {
    background-color: black;
    color: white;
    height: 100%;
    padding: 0 4px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.content-input span {
    font-size: 14px;
    color: rgb(114, 105, 105);
}

.upload-card input[type="file"] {
    width: 100%;
    padding: 4px;
}

.upload-card input[type="text"],
textarea {
    width: 80%;
    padding: 4px;
    border: 1px solid black;
}

.upload-button {
    width: 80%;
    background-color: black;
    color: white;
    padding: 4px 8px;
    border: 0;
    outline: 0;
}